
button.add-to-cart {
  width: 120px;
  height: 30px;
  display: block;
  margin: 8px auto 0;
  background: #fd926d;
  color: #FFF;
  font-size: 12px;
  text-transform: uppercase;
  outline: 0;
  border: 0;
  cursor: pointer;
  border-bottom: 3px solid #dd6b44;
}
button.add-to-cart:after {
    content: "";
    display: inline-block;
    width: 11px;
    height: 11px;
    margin-left: 8px;
    position: relative;
    top: -1px;
    vertical-align: middle;
    background-image: url("../assets/ic-plus-small.png");
    background-position: center center;
    background-repeat: no-repeat;
    background-size: contain;
}



